.popup{
  position: fixed;
  top: 0;
  left: 0;

  height: 100%;
  width: 100%;

  background: rgba(35, 43, 63, 0.5);
  backdrop-filter: blur(15px);


  opacity: 0;
  z-index: -1;

  transition: .5s all;

  &.active{
    opacity: 1;
    z-index: 99;
  }
  &-photo{
    &__cropp{
      margin-top: 30px;
    }
  }
  &__wrapper{
    height: 100%;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    padding: 25px;
  }
  &__content{
    max-width: 460px;
    width: 100%;
    max-height: 100%;

    overflow-y:auto;
    &_small{
      max-width: 370px;
      padding: 25px 20px;
    }
  }
  &__close{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
  }
  &-trigger{
    &:hover{
      cursor: pointer;
    }
  }
}

.cropp{
  &__img{
    width: 100%;

    height: 180px;

    img{
      width: 100%;
      height: 100%;

      object-fit:cover;
    }
  }
}